const formProduct = () => {
	let productAdd = document.querySelectorAll('.js-product-add');
	let productSectionList = document.querySelectorAll('.js-product-list');

	productAdd.forEach(function(element) {
		element.addEventListener('click', function() {
			let productSections = this.closest('.js-product-section');
			let productList = productSections.querySelector('.js-product-list');
			let productItems = productSections.querySelectorAll('.js-product-item');

			productList.innerHTML += productItems[0].outerHTML;

			numberReplacement(productSections, productItems.length);
		});
	});

	productSectionList.forEach(function(element) {
		element.addEventListener('click', function(event) {			
			let target = event.target;

			if (target.tagName != 'BUTTON') return;

			let productItem = target.closest('.js-product-item');
			productItem.remove();
		});
	});

	function numberReplacement(productSections, number) {
		let productItems = productSections.querySelectorAll('.js-product-item'); 
		
		let productNumber = productItems[number].querySelector('.js-product-number');

		productNumber.innerHTML = ++number;
	}
}

export default formProduct;