const linerDesignItem = () => {
	let items = document.querySelectorAll('.liner-design-item');

	items.forEach((element) => {
		element.addEventListener('click', function() {
			items.forEach((element2) => {
				element2.classList.remove('liner-design-item--active');
				element2.querySelector('.checkbox input').checked = false;
			});

			element.classList.add('liner-design-item--active');
			let checkbox = this.querySelector('.checkbox input');			
			checkbox.checked = true;
		});
	});
}

export default linerDesignItem;